/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: #f4f6f8;
  color: #333;
  line-height: 1.7;
}

/* Navbar */
.navbar {
  background: #ffffff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav-container {
  max-width: 1100px;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-logo {
  font-weight: bold;
  font-size: 1.2rem;
  color: #007acc;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  text-decoration: none;
  color: #444;
  font-weight: 500;
}

.nav-links a:hover {
  color: #007acc;
}

/* Hero */
.hero {
  background: linear-gradient(135deg, #007acc, #00c9ff);
  color: white;
  padding: 100px 20px;
  text-align: center;
}

.name {
  font-size: 3rem;
  font-weight: 700;
}

.title {
  font-size: 1.2rem;
  margin-top: 0.5rem;
  color: #e0f4ff;
}

.intro {
  margin-top: 1.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
}

/* Sections */
.section {
  max-width: 800px;
  margin: 80px auto;
  padding: 0 20px;
  text-align: center;
}

.section h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #007acc;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
  list-style: none;
}

.skills-list li {
  background-color: #fff;
  padding: 0.6rem 1.2rem;
  border-radius: 30px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.06);
  font-weight: 500;
}

/* Projects */
.project {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  margin-top: 1.5rem;
  box-shadow: 0 1px 6px rgba(0,0,0,0.05);
  text-align: left;
}

/* Footer */
.footer {
  padding: 2rem;
  text-align: center;
  font-size: 0.9rem;
  color: #777;
  background-color: #ffffff;
  margin-top: 100px;
}
